.navbar * {
    font-family: "Arial Rounded MT Bold";
    font-size: 24px;
    /*   color: #000000; */
}

.navbar-menu a.navbar-item {
    font-size: 18px;;
}

nav.navbar, span, a {
    /*   border: 1px solid tomato; */
}


.navbar-brand a.navbar-item:hover{
    color: black !important;
}

.navbar-menu .navbar-item {
    margin-right: 20px;

}
span.icon {
    padding: 0 20px;
}



/*Center this and its children*/
.centerthis, .centerthis *{
    justify-content: center;
}

/* Added custom css here */
.field {
    padding: 10px;
}

.icon {
    color: #BCBFC8;
}

.input{
    background-color: #F5F6FA;
}

.field .control span.icon {
    color: #000000;
}

.selected {
    border-bottom: 4px solid orange;
    border-radius: 2px;
}

.selected .icon {
    color: orange;
}

.profile-img {
    width: 80px;
    height: 80px;
    border-radius: 80px;
    border: 2px solid black;
    background-color: #f1f1f1f1;
    margin-top: 5px;
    margin-right: 20px;
}

.dropdown {
    margin-top: 20px;
}

.dropdown .button{
    border: none;
}


svg {
   z-index: 0;
}

.navbar-brand .burger{
    border: none;
}

.navbar-end figure.image{
    padding-top: 14px;
}